<template>
<div class="tags-wrapper">
  <h3 class="title">
    Filter stories by theme
  </h3>
  <div class="tags">
    <button type="button" v-bind:class="{
        'tag-active': selected.indexOf(tag) >= 0,
        'tag' : true
        }" @click="clickOnTag(tag)" v-for="(tag, i) in tags" :key="i">
      {{ tag }}
    </button>

    <button type="button" class="tag-reset tag" @click="$emit('reset')">Reset</button>
  </div>
</div>
</template>
<script>
export default {
  name: "tags",
  props: {
    values: Array,
    tags: Array
  },
  data() {
    return {
      selected: []
    }
  },
  watch: {
    values: function(newValue) {
      if (this.values)
        this.selected = this.values;
    },
    // watch selected change
    selected: function() {
      // emit custom event
      this.$emit('input', this.selected);
    }
  },
  mounted() {
    if (this.values)
      this.selected = this.values;
  },
  computed: {
    // allSelected() {
    //   if (this.selected.length == this.tags.length) {
    //     return true;
    //   }
    //
    //   return false;
    // }
  },
  methods: {
    clickOnTag(tag) {

      var index = this.selected.indexOf(tag);
      // if not select - select
      if (index < 0) {
        // add to array
        this.selected.push(tag.trim().toLowerCase())
      } else {
        // remove from array
        this.selected.splice(index, 1);
      }


      this.$emitToWindow('tagClick', tag);

    },
    selectAll() {
      if (!this.allSelected) {
        this.selected = [];
        this.tags.forEach(tag => {
          this.selected.push(tag)
        });
      } else {
        this.selected = [];
      }
    }
  }

}
</script>

