<template>
<div class="dropdown">
  <a class="input dropdown-button" ref="dropdownButton" href="javascript:void(0)" @click="isShowing = !isShowing">{{ value }}

    <svg viewBox="0 0 16 16" xmlns="http://www.w3.org/2000/svg" :class="{'down' : !isShowing}">
      <polygon points="7.302 12.227 9.578 14.546 16 8 9.578 1.454 7.302 3.772 9.841 6.361 0 6.361 0 9.64 9.841 9.64" fill="#000000" fill-rule="evenodd" />
    </svg>
  </a>
  <ul :class='{"show" : isShowing}'>
    <li :class="{'active' : selected == -1}">
      <a href="javascript:void(0)" @click="setIndex(-1)">
        Any
      </a>
    </li>
    <li v-for="(item, i) in items" :key="i" :class="{'active' : selected == i}">
      <a href="javascript:void(0)" @click="setIndex(i)">
        {{ item }}
      </a>
    </li>
  </ul>
</div>
</template>

<script>
export default {
  name: 'Dropdown',
  props: {
    title: String,
    items: Array,
  },
  data() {
    return {
      isShowing: false,
      selected: undefined
    };
  },
  computed: {
    value() {
      return (this.selected >= 0 && this.items[this.selected]) ? this.items[this.selected] : this.title;
    }
  },
  mounted() {
    document.body.addEventListener('click', (e) => {
      if (e.target != this.$refs.dropdownButton) {
        this.isShowing = false;
      }
    });
  },
  methods: {
    setIndex(i) {
      this.selected = i;
      this.isShowing = false;
      this.$emit("change", this.items[this.selected]);
    }
  }
}
</script>

