  width: 23px;
  height: 23px;
  pointer-events: none;
  vertical-align: middle;
  float: right;
}

#app a svg.down {
  transform: rotate(90deg);
  -moz-transform: rotate(90deg);
  -webkit-transform: rotate(90deg);
}


#app .dropdown ul {
  width: 100%;
  display: none;
  z-index: 100;
  max-height: 14rem;
  overflow: scroll;
  position: absolute;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.4);

  background: #fff;
  border-radius: 4px;
}

