  display: block;
  list-style: none;
  padding: 0;
  margin: 0;
}

#app .dropdown li {
  width: 100%;
  box-sizing: border-box;
  line-height: 1.2rem;
  padding: 0;
  margin: 0;
}

#app .dropdown li a {
  padding: 0.4rem 1rem;
  height: 100%;
  box-sizing: border-box;
  width: 100%;

}

