          <g id="Page-1" stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
            <g id="Desktop_Story" transform="translate(-715.000000, -33.000000)" fill="#FFFFFF" fill-rule="nonzero">
              <g id="Overlay">
                <g id="Close" transform="translate(706.000000, 24.000000)">
                  <path
                    d="M26.7037037,31 C27.1666667,31 27.6296296,30.8148148 27.962963,30.4814815 L30.4814815,27.962963 C30.8148148,27.6296296 31,27.1666667 31,26.7037037 C31,26.2407407 30.8148148,25.7777778 30.4814815,25.4444444 L25.037037,20 L30.4814815,14.5555556 C30.8148148,14.2222222 31,13.7592593 31,13.2962963 C31,12.8333333 30.8148148,12.3703704 30.4814815,12.037037 L27.962963,9.51851852 C27.6296296,9.18518519 27.1666667,9 26.7037037,9 C26.2407407,9 25.7777778,9.18518519 25.4444444,9.51851852 L20,14.962963 L14.5555556,9.51851852 C14.2222222,9.18518519 13.7592593,9 13.2962963,9 C12.8333333,9 12.3703704,9.18518519 12.037037,9.51851852 L9.51851852,12.037037 C9.18518519,12.3703704 9,12.8333333 9,13.2962963 C9,13.7592593 9.18518519,14.2222222 9.51851852,14.5555556 L14.962963,20 L9.51851852,25.4444444 C9.18518519,25.7777778 9,26.2407407 9,26.7037037 C9,27.1666667 9.18518519,27.6296296 9.51851852,27.962963 L12.037037,30.4814815 C12.3703704,30.8148148 12.8333333,31 13.2962963,31 C13.7592593,31 14.2222222,30.8148148 14.5555556,30.4814815 L20,25.037037 L25.4444444,30.4814815 C25.7777778,30.8148148 26.2407407,31 26.7037037,31 Z">
