              <svg width="100%" height="100%" viewBox="0 0 86 86" version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink">
                <g stroke-width="1" fill="none" fill-rule="evenodd">
                  <g transform="translate(-164.000000, -579.000000)">
                    <g transform="translate(16.000000, 497.000000)">
                      <g transform="translate(148.000000, 82.000000)">
