        </a>

        <div class="tags-wrapper">
          <div class="tags">
            <button type="button" class='tag tag-active' v-for="(tag, n) in themeTags" :key="n" @click="clickOnTag(tag.trim().toLowerCase())">{{tag}}</button>
          </div>
        </div>
