<script>
import * as L from 'leaflet'

export default {
  name: 'Popup',
  props: {
    baseUrl: String,
    items: Array,
    geoJson: Object,
    center: Array,
    itemIndex: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      map: undefined,
      visible: true,
      counter: 0,
      marker: undefined,
      item: undefined

    }
  },
  mounted() {

    this.setCounter(this.itemIndex);

    this.map = L.map('popup-map', {
      continuousWorld: true,
      zoomControl: false,
      dragging: false,
      scrollWheelZoom: false,
      static: true
    }).setView(this.center ? this.center : [54.904571379248125, -3.436872187499991], 7.5);

    L.geoJson(this.geoJson, {
      clickable: true,
    }).addTo(this.map);

    this.updateMarker();

  },
  watch: {
    center: function() {
      this.updateMarker();
    },
    items: function() {
      this.updateMarker();
    },
  },
  methods: {
    setCounter(e) {
      this.counter = e;

      if (this.items[this.counter]) {
        this.item = this.items[this.counter];
        this.$emitToWindow('storyOpen', this.item);
      }
    },
    updateMarker() {
      if (this.map) {
        if (this.marker) {
          this.map.removeLayer(this.marker);
        }

        this.marker = L.marker(this.center, {
          icon: L.divIcon({
            html: '<svg width="32px" fill="#000000" version="1.1" x="0px" y="0px" viewBox="0 0 32 50.036" enable-background="new 0 0 100 100" xmlns="http://www.w3.org/2000/svg" xmlns:bx="https://boxy-svg.com"><circle style="fill: rgb(255, 255, 255);" cx="16" cy="21" r="9.625"/><g transform="matrix(0.440792, 0, 0, 0.440792, -6.039605, 4.79604)"><path d="M50,5C32.459,5,18.239,19.22,18.239,36.761C18.239,54.302,50,95,50,95s31.761-40.698,31.761-58.239   C81.761,19.22,67.541,5,50,5z M50.183,56.702c-11.206,0-20.29-9.084-20.29-20.29c0-11.205,9.084-20.289,20.29-20.289   c11.206,0,20.289,9.084,20.289,20.289C70.473,47.618,61.389,56.702,50.183,56.702z" style="fill: rgb(245, 219, 55); fill-opacity: 1;"/></g><text x="15.993" y="26.593" style="white-space: pre; fill: rgb(51, 51, 51); font-size: 14.3px; text-anchor: middle;">' +
              this.items.length + '</text></svg>',
            iconSize: new L.Point(32, 50),
            iconAnchor: new L.Point(16, 50)
          })
        }).addTo(this.map);

        this.map.panTo(new L.LatLng(this.center[0], this.center[1]));
      }
    },
    getId(url) {
      return this.$youtube.getIdFromUrl(url)
    },
    // playVideo() {
    //   this.player.playVideo()
    // },
    // stopVideo() {
    //   this.player.stopVideo()
    // },
    close() {
      this.$emit("close");
    },
    clickOnTag(tag) {
      this.$emit('onTagsChange', [tag]);
      this.$emitToWindow('tagClick', tag);
    }
  },
  computed: {
    isMobile() {
      return window.innerWidth <= 768;
    },
    player() {
      return this.$refs.youtube.player
    },
    itemText() {
      return this.lquo + this.item.text + this.rquo;
      // return '<span class="quo left-quo"><i>&ldquo;</i></span>' + this.item.text + '<span class="quo right-quo"><i>&bdquo;</i></span>';
    },
    lquo() {
      return '<div class="quo left-quo"><svg viewBox="0 0 500 500" xmlns="http://www.w3.org/2000/svg"><path d="M13.43 460.275C2.834 450.34.848 445.705.583 431.005c0-6.755 0-55.362.132-108.207.133-52.845 0-100.128-.53-105.293-.53-7.152 0-12.847 2.517-24.502 5.828-26.754 9.933-41.19 13.244-46.223 1.855-2.782 5.563-9.404 8.212-14.834 14.039-27.813 44.766-57.613 75.493-73.109 23.972-12.185 48.74-20.132 76.288-24.502 19.204-3.046 25.43-1.854 33.508 6.225l5.43 5.43 1.06 28.873c1.192 32.448.397 39.6-5.828 49.401-5.562 8.742-7.02 9.669-21.72 12.98-12.98 3.046-25.562 8.476-42.78 18.41-7.284 4.238-9.139 6.224-14.171 14.833-3.179 5.43-7.682 14.966-10.066 21.191-3.841 10.33-4.238 12.847-4.768 31.522l-.662 20.131h36.82c36.819 0 36.951 0 46.752 3.576 9.536 3.444 9.8 3.709 13.244 10.86l3.444 7.418.132 87.413c.133 103.836.133 103.041-10.728 109.796l-6.622 4.106-89.135.397-89.135.397-7.284-7.02zM297.522 460.937c-9.933-9.271-12.847-16.026-12.847-28.873 0-5.827.265-52.183.53-103.041.397-50.859.133-98.671-.53-106.22-1.06-12.185-.794-15.496 1.855-28.078 5.827-27.681 10.463-43.575 14.039-49.005 1.854-2.914 5.562-9.403 8.079-14.436 2.516-5.033 8.344-14.304 12.98-20.397 29.667-40.13 79.598-67.414 139.595-76.685 19.072-2.913 23.84-1.986 31.654 6.49l5.695 6.093.927 31.124c1.06 34.965.53 38.541-7.416 49.402l-4.636 6.49-15.76 3.84c-11.92 2.914-19.073 5.563-29.668 11.125-7.55 4.106-15.496 8.61-17.483 9.934-5.033 3.576-13.244 17.747-18.94 32.978-4.502 11.655-4.767 13.774-5.562 32.052l-.795 19.601h74.964l9.668 3.576c9.271 3.444 9.801 3.841 12.715 10.331l3.046 6.622.265 88.076c.265 75.36.132 89.4-1.722 97.61l-1.987 9.537-8.079 4.106-8.079 3.973H304.145l-6.623-6.225z"/></svg></div>';
    },
    rquo() {
      return '<div class="quo right-quo"><svg viewBox="0 0 500 500" xmlns="http://www.w3.org/2000/svg"><path d="M18.645 466.596c-4.105-.927-7.284-3.046-11.522-7.55l-5.695-6.092L.5 421.83c-1.06-34.965-.53-38.541 7.416-49.402l4.636-6.49 15.76-3.84c11.92-2.914 19.073-5.563 29.668-11.126 7.55-4.105 15.496-8.609 17.483-9.933 5.033-3.576 13.244-17.747 18.94-32.979 4.502-11.655 4.767-13.774 5.562-32.051l.795-19.602H25.797l-9.668-3.576c-9.271-3.443-9.8-3.84-12.715-10.33l-3.046-6.623-.265-88.075c-.265-75.36-.132-89.4 1.722-97.611l1.987-9.536 8.079-4.106 8.079-3.973h175.885l6.623 6.225c9.933 9.27 12.847 16.026 12.847 28.873 0 5.827-.265 52.183-.53 103.041-.397 50.859-.132 98.67.53 106.22 1.06 12.185.794 15.496-1.854 28.078-5.828 27.681-10.464 43.574-14.04 49.005-1.854 2.913-5.562 9.403-8.079 14.436-10.198 20.131-26.886 39.203-49.534 56.421-25.296 19.204-63.175 34.436-100.392 40.263-6.49 1.06-13.112 2.12-14.57 2.384-1.456.132-5.164-.132-8.21-.927zM302.34 466.199c-4.37-1.192-8.079-3.312-11.787-7.02l-5.43-5.43-1.06-28.873c-1.192-32.449-.397-39.6 5.828-49.402 5.562-8.74 7.02-9.668 21.72-12.98 12.98-3.045 25.562-8.475 42.78-18.409 7.284-4.238 9.139-6.225 14.171-14.833 3.18-5.43 7.682-14.967 10.066-21.192 3.841-10.33 4.238-12.847 4.768-31.389l.662-20.264H347.24c-36.82 0-36.952 0-46.753-3.576-9.536-3.443-9.8-3.708-13.244-10.86l-3.444-7.417-.132-87.413c-.133-103.836-.133-103.041 10.728-109.796l6.622-4.106 89.135-.397 89.135-.398 7.284 7.02c10.596 9.933 12.582 14.569 12.847 29.27 0 6.755 0 55.362-.132 108.207-.133 52.845 0 100.26.53 105.293.53 7.152 0 12.847-2.517 24.502-5.828 26.754-9.933 41.19-13.244 46.223-1.855 2.781-5.563 9.404-8.212 14.834-14.039 27.813-44.766 57.613-75.493 73.109-23.972 12.185-48.74 20.131-76.288 24.502-12.847 1.987-16.29 2.119-21.72.795z"/></svg></div>';
    },
    themeTags() {
      if (this.item) {
        return this.item.themeTags.split(',');
      }
      return [];
    },
  }
}
</script>

