import Vue from 'vue'
import App from './App.vue'
import VueYoutube from 'vue-youtube'
import VBodyScrollLock from 'v-body-scroll-lock'

Vue.config.productionTip = false
Vue.use(VueYoutube);
Vue.use(VBodyScrollLock);

Vue.prototype.$emitToWindow = (event, data) => {
  if (window.PGRConfig && window.PGRConfig.events) {

    var listener = window.PGRConfig.events[event];
    if (listener) {
      // to prevent issues
      setTimeout(() => {
        listener(data);
      })
    }
  }

};

new Vue({
  render: h => h(App),
}).$mount('#app')
